@import '../../style/mixin.less';
.wrapper{
    height: 100%;
    overflow: hidden;
    .list_row_wrapper{
        margin-bottom: 0.2rem;
        &>img{
            display: block;
            width: 100%;
            height: 4.5rem;
            max-height: 500px;
        }
        &_content{
            background-color: @white;
            padding: 8px 8px 0;
            &>b{
                margin-bottom: 4px;
            }
            &_footer{
                display: flex;
                color: @shallowGrey;
                padding: 8px 0;
                margin-top: 8px;
                &>li{
                    flex: 1;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    &>i{
                        margin-right: 4px;
                    }
                }
            }
        }
    }
}
.filter_wrapper{
    &>li:first-child{
        padding: 8px 10px;
        background-color: @bgGrey;
    }
    &>li:last-child{
        padding: 10px 0 10px 10px;
        .filter_item_wrapper{
            &>li{
                display: inline-block;
                vertical-align: top;
                width: 25%;
                padding: 10px 0;
                margin: 0 8.33% 10px 0;
                text-align: center;
                border: 1px solid @normalGrey;
                .oneRowOverflowOmit();
            }
            .active{
                border-color: @red;
                color: @red;
            }
        }
    }
}