.tabs_content {
  padding: 20px 0 20px 20px;
}
.tabs_content > li {
  display: inline-block;
  vertical-align: top;
  width: 25%;
  padding: 10px 0;
  text-align: center;
  margin-right: 8.333%;
  margin-bottom: 15px;
  border: 1px solid #DDD;
  max-width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.tabs_content .active {
  border-color: #FF0000;
  color: #FF0000;
}
.list_row_wrapper {
  display: inline-block;
  width: 49%;
  background-color: #FFF;
  margin-bottom: 0.2rem;
}
.list_row_wrapper > div:first-child {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 4.5rem;
  max-height: 500px;
}
.list_row_wrapper > div:first-child > img {
  width: 80%;
  height: 80%;
}
.list_row_wrapper > div:last-child {
  height: 50px;
  font-size: 14px;
  padding: 4px 8px;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
}
