.wrapper {
  display: flex;
  align-items: center;
  padding: 0 8px;
  text-align: center;
  color: #AEAEAE;
}
.wrapper > li {
  flex: 1;
}
.wrapper > li > div {
  border-right: 1px dashed #DDD;
}
.wrapper > li::after {
  content: '';
  display: block;
  width: 50%;
  height: 1px;
  margin: 4px auto auto auto;
  background-color: transparent;
}
.wrapper .active {
  color: #FF0000;
}
.wrapper .active::after {
  background-color: #FF0000;
}
