.wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.wrapper > div:first-child {
  flex: 1;
  overflow-y: auto;
}
.wrapper > div:last-child {
  display: flex;
  height: 43.5px;
  line-height: 43.5px;
  font-size: 16px;
  text-align: center;
}
.wrapper > div:last-child > span {
  flex: 1;
}
.wrapper > div:last-child > span:last-child {
  color: #FFF;
  background-color: #FF0000;
}
