.wrapper {
  height: 100%;
  overflow: hidden;
}
.wrapper .list_row_wrapper {
  margin-bottom: 0.2rem;
}
.wrapper .list_row_wrapper > img {
  display: block;
  width: 100%;
  height: 4.5rem;
  max-height: 500px;
}
.wrapper .list_row_wrapper_content {
  background-color: #FFF;
  padding: 8px 8px 0;
}
.wrapper .list_row_wrapper_content > b {
  margin-bottom: 4px;
}
.wrapper .list_row_wrapper_content_footer {
  display: flex;
  color: #AEAEAE;
  padding: 8px 0;
  margin-top: 8px;
}
.wrapper .list_row_wrapper_content_footer > li {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.wrapper .list_row_wrapper_content_footer > li > i {
  margin-right: 4px;
}
.filter_wrapper > li:first-child {
  padding: 8px 10px;
  background-color: #F7F7F7;
}
.filter_wrapper > li:last-child {
  padding: 10px 0 10px 10px;
}
.filter_wrapper > li:last-child .filter_item_wrapper > li {
  display: inline-block;
  vertical-align: top;
  width: 25%;
  padding: 10px 0;
  margin: 0 8.33% 10px 0;
  text-align: center;
  border: 1px solid #DDD;
  max-width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.filter_wrapper > li:last-child .filter_item_wrapper .active {
  border-color: #FF0000;
  color: #FF0000;
}
