@import '../../style/mixin.less';
.mask{
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 9999;
    max-width: @maxWidth;
    margin: auto;
    background-color: rgba(0, 0, 0, 0.3);
    transition: transform 0.2s linear;
    transform: translateX(100%);
    &_wrapper{
        position: relative;
        height: 100%;
        &_content{
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 20%;
            height: 100%;
            background-color: @white;
        }
    }
}