.mask {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 9999;
  max-width: 1024px;
  margin: auto;
  background-color: rgba(0, 0, 0, 0.3);
  transition: transform 0.2s linear;
  transform: translateX(100%);
}
.mask_wrapper {
  position: relative;
  height: 100%;
}
.mask_wrapper_content {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 20%;
  height: 100%;
  background-color: #FFF;
}
